.modal {
  height: fit-content;
  max-height: 98%;
  min-width: 300px;
  width: fit-content;
  max-width: 96%;
  transition: transform var(--timing-function__main), visibility var(--timing-function__main);
  background-color: #fff;
  border-radius: 4px;
  flex-direction: column;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow: hidden;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.modal-name {
  text-align: center;
  margin: 0 auto;
  font-size: 18px;
}

.modal-top {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.modal-overflow {
  max-height: 100%;
  min-height: 70%;
  flex-direction: column;
  justify-content: start;
  display: flex;
  overflow: auto;
}

.modal-content {
  padding: 40px;
}

.modal .--emptyCartContent {
  text-align: center;
  font-size: 20px;
  font-weight: 600;
  line-height: 1.3;
}

@media screen and (min-width: 500px) {
  .modal {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .modal {
    height: fit-content;
    max-height: 98%;
    min-width: 480px;
    width: fit-content;
    max-width: 800px;
  }
}

@media screen and (min-width: 1200px) {
  .modal {
    height: fit-content;
    max-height: 98%;
    max-width: 1000px;
  }
}

.backdrop {
  z-index: 1010;
  width: 100%;
  height: 100%;
  opacity: 1;
  transition: opacity var(--timing-function__main), visibility var(--timing-function__main);
  background-color: rgba(0, 0, 0, .2);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.backdrop.is-hidden .modal {
  transform: translate(-50%, -50%)scale(.8);
}

.--notScrolled {
  overflow: hidden;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  display: none;
}

.button {
  letter-spacing: .05em;
  text-align: center;
  transition: all var(--timing-function__main);
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 4px;
  line-height: 1.2;
  display: block;
}

.button:hover {
  background-color: #82ff7b;
  box-shadow: 0 0 8px rgba(0, 0, 0, .2);
}

.button:active {
  box-shadow: 0 0 3px rgba(0, 0, 0, .2);
}

.button.--close-modal {
  width: 30px;
  height: 30px;
  fill: #000;
  transition: box-shadow var(--anim-main-timing-function);
  background-color: #fff;
  justify-content: center;
  align-self: flex-start;
  align-items: center;
  display: flex;
}

.button.--close-modal .btn-icon {
  width: 12px;
  height: 12px;
  min-width: 12px;
  min-height: 12px;
}

.button.--close-modal:hover, .button.--close-modal:focus {
  fill: var(--brand_clr__1--accent);
  background-color: rgba(225, 225, 225, .3);
}

.button.--close-modal:active {
  background-color: rgba(225, 225, 225, .6);
}

.button.--form-btn {
  min-height: 30px;
  padding: 8px 16px;
}

.button.--loadMoreBtn {
  min-height: 30px;
  margin: auto;
  padding: 8px 16px;
}

.form {
  justify-content: center;
  align-items: center;
  gap: 8px;
  display: flex;
}

.form-input {
  height: 30px;
  width: 250px;
  transition: all var(--timing-function__main);
  border-style: none none solid;
  border-bottom-width: 2px;
  border-bottom-color: #b4b4b4;
  outline-style: none;
  padding: 0 16px;
}

.form-input:hover, .form-input:focus {
  border-bottom: 2px solid #82ff7b;
}

.container {
  max-width: 1200px;
  margin: auto;
}

.gallery {
  width: 100%;
  flex-wrap: wrap;
  gap: 8px;
  margin: 8px 0;
  display: flex;
}

.card {
  max-width: 250px;
  position: relative;
}

.card .img {
  width: 100%;
  max-height: 250px;
  display: block;
}

.card:hover {
  box-shadow: 0 0 5px rgba(0, 0, 0, .3);
}

.card-bottom {
  max-width: 100%;
  background-color: #fff;
  grid-template-columns: repeat(2, 1fr 3fr);
  align-items: center;
  gap: 2px;
  padding: 2px;
  display: grid;
  position: -webkit-sticky;
  position: sticky;
  top: 100%;
  left: 0;
}

.card-bottom .col-title {
  text-align: center;
  text-align: left;
  padding: 2px;
  font-size: 10px;
  font-weight: 600;
}

.card-bottom .col-text {
  text-align: left;
  text-align: right;
  padding: 2px 8px;
  font-size: 10px;
}

